.hasTooltip {
    position: relative;
}

$primaryColor: #007097;
$primaryAccent: #80B1CB;
$toolColor: #bbb;
$toolHover: #eee;
$toolActive: #dfdfdf;
$lightYellow: #FFFFE9;
$darkGreen: #007815;
$yellow: #F8CE00;
$footerBackground: #0c0c23;

$colors: (
    'primaryColor': #007097,
    'primaryAccent': #80B1CB,
    'toolColor': #bbb,
    'toolHover': #eee,
    'toolActive': #dfdfdf,
    'lightYellow': #FFFFE9,
    'darkGreen': #007815,
    'yellow': #F8CE00,
    'footerBackground': #0c0c23
);

:export {

    @each $key,
    $value in $colors {
        #{unquote($key)}: $value;
    }
}