@use 'global.module.scss' as *;

#detailedTable {
    background-color: white;
    margin: 1rem;
    border-radius: 1rem;
    border: 1px solid #ddd;
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: scroll;
}

#detailedTableBanner {
    margin: 0;
    background-color: $primaryAccent;
    width: 100%;
    height: 60px;
    font-size: 18pt;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px 12px 0 0;
    user-select: none;
}

#filters {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
}

.filter {
    margin: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;

    p {
        min-width: 200px;
        margin: 0 0 6px 0;
        user-select: none;
    }
}

.filterInput {
    width: 200px;
    height: 30px;
    margin: 0 auto;
    padding: 0 0 0 9px;
    border: none;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    &::placeholder {
        font-style: italic;
        user-select: none;
    }

    &:focus {
        outline: $primaryColor auto 1px;
    }
}

.tableSpan {
    margin: 0 0 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.iconDiv {
    margin: 0 6px 0 auto;
}

.icon {
    color: $toolColor;
    padding: 9px;
    font-size: 18pt;
    border-radius: 50%;
    transition: all .2s ease-in-out;
    cursor: pointer;

    &:hover {
        color: #007097;
        background-color: $toolHover;
        scale: 1.05;
        transition: all .2s ease-in-out;
    }

    &:active {
        background-color: $toolActive;
        scale: 1;
        transition: all .2s ease-in-out;
    }
}

.tableDiv {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    height: 500px;
    overflow: scroll;

    &::-webkit-scrollbar {
        -webkit-appearance: none;
    }

    &::-webkit-scrollbar:vertical {
        width: 12px;
    }

    &::-webkit-scrollbar:horizontal {
        height: 12px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 8px;
        border: 2px solid white;
        background-color: rgba(0, 0, 0, .27);
    }

    &::-webkit-scrollbar-track {
        border-radius: 6px;
        box-shadow: inset 0 0 9px rgba(0, 0, 0, .18);
    }
}

table {
    margin: 0;
    border-spacing: 0;
    width: 100%;

    thead {
        background-color: white;
        margin: 0;
        position: sticky;
        top: 0;
        height: 60px;
        user-select: none;
    }

    tbody {
        margin: 0;
    }

    td {
        font-size: 10pt;
        margin: 0;
        padding: 6px;
        border-bottom: 1px solid #ddd;
        border-right: 1px solid #ddd;
    }

    th {
        margin: 0;
        padding: 3px 15px;
        min-width: 200px;
        width: fit-content;
        position: relative;
        top: 1px;
        font-size: 12pt;
        font-weight: normal;
        height: 60px;
        border-top: 1px solid #ddd;
        border-bottom: 4px solid #ddd;
        border-right: 1px solid #ddd;
        cursor: grab;

        &:hover {
            background-color: $toolHover;
        }
    }

    tbody tr {
        &:hover {
            background-color: $toolHover;
        }
    }
}

.sortButton {
    background-color: rgba(white, 0);
    color: $toolColor;
    margin: 0 0 0 3px;
    padding: 0;
    font-size: 9pt;
    border: none;
    cursor: pointer;
    transition: all .2s ease-in-out;

    &:hover {
        background-color: rgba($toolHover, 1);
        color: $primaryColor;
        transition: all .2s ease-in-out;
    }

    &:active {
        background-color: rgba($toolActive, 1);
        transition: all .2s ease-in-out;
    }
}

.link {
    color: $primaryColor;
    text-decoration: none;
}

.handle {
    position: absolute;
    top: 0;
    right: -2px;
    width: 4px;
    height: 100%;
    cursor: col-resize;
    opacity: 0;

    .handle:hover {
        background-color: black;
        opacity: 1;
    }
}


#tableControls {
    margin: auto 0 0 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0 12px;
    height: 69px;
    width: 100%;
    border-top: 1px solid #ddd;
}

#amountOfRowsToDisplay {
    display: flex;
    align-items: center;
    margin: 0 12px 0 0;
}

#rowAmountSelect {
    width: 60px;
    height: 24px;
    margin: 0 12px 0 0;
    border: none;
    border-bottom: 2px solid black;
}

#rowAmountSelect:focus {
    outline: rgba(0, 0, 0, 0) auto 1px;
    border-bottom: 2px solid #007097;
}

.tableButton {
    background-color: white;
    border: none;
    padding: 0 12px;
    height: 40px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 0 12px 0 0;
    cursor: pointer;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);
    transition: all 0.1s ease-in-out;

    &:hover {
        scale: 1.05;
        box-shadow: 0 3px 4px 1px rgba(0, 0, 0, .18);
        transition: all 0.1s ease-in-out;
    }

    &:active {
        scale: 1;
        box-shadow: inset 0 1px 2px 1px rgba(0, 0, 0, .18);
        transition: all 0.1s ease-in-out;
    }
}


#viewRowSearch {
    width: 120px;
    height: 40px;
    padding: 0 12px;
    margin: 0;
    border: none;
    border-radius: 6px;
    box-shadow: 0 1px 4px 1px rgba(0, 0, 0, .18);

    &::placeholder {
        font-style: italic;
    }

    &:focus {
        outline: #007097 auto 1px;
    }
}


#currentRowIndexCount {
    display: flex;
    align-items: center;
    margin: 0 12px 0 auto;
}