#container {
    color: rgb(121, 128, 171);
    background-color: rgb(12, 12, 35);
    margin: auto 0 0 0;
    height: 78px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    user-select: none;

    a {
        color: rgb(121, 128, 171);
        margin: 6px 0;
        text-decoration: none;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: max-content;

        img {
            height: 40px;
        }

        p {
            margin: auto 6px 0 0;
        }
    }

    p {
        color: rgb(121, 128, 171);
        margin: 0 0 6px 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}