@use "global.module.scss" as *;

.navbar {
    background-color: $primaryColor;
    padding: 1.5rem;
    display: flex;
    flex-direction: row;
}

.navLink {
    color: white;
    margin: 0 10px;
    font-size: 1.5rem;
    text-decoration: none;
    transition: all 0.2s ease-in-out;
    width: 6rem;
    text-align: center;

    &:hover {
        color: $toolHover;
    }

    &:active {
        color: $toolActive;
    }
}

.active {
    font-weight: bold;

    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 2px;
        background-color: white;
        margin: 0 auto;
        margin-top: 5px;
        transition: all 0.2s ease-in-out;
    }
}
    
.navLinks {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-grow: 1;
}

.logo {
    height: 1.8rem;
}