@use "global.module.scss" as *;

.container {
    background-color: white;
    margin: 52px 30px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    right: 0;
    max-height: 600px;
    border-radius: 9px;
    box-shadow: 0 4px 10px 1px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in-out;
    z-index: 1;
    opacity: 0;

    &::-webkit-scrollbar {
        -webkit-appearance: none;
    }

    &::-webkit-scrollbar:vertical {
        width: 12px;
    }

    &::-webkit-scrollbar:horizontal {
        height: 0;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 8px;
        border: 2px solid white;
        background-color: rgba(0, 0, 0, .27);
    }

    &::-webkit-scrollbar-track {
        background-color: white;
        border-radius: 9px;
    }
}

.columnSelect {
    margin: 6px 0;
    font-size: 15pt;
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    z-index: 1;
    user-select: none;
}

.checkMark {
    color: $toolColor;
    margin: 0 9px 0 0;
    padding: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: all 0.2s ease-in-out;

    &:hover {
        background-color: $toolHover;
        transition: all 0.2s ease-in-out;
    }

    &:active {
        background-color: $toolActive;
        transition: all 0.2s ease-in-out;
    }

    input {
        width: 18px;
        height: 18px;
    }
}

.gripLines {
    color: $toolColor;
    margin: 0 9px;
    padding: 6px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
    cursor: grab;

    &:active {
        color: $toolActive;
        cursor: grabbing;
        transition: all 0.2s ease-in-out;
    }
}

.toolbar {
    display: flex;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.tools {
    margin: 0 0 0 auto;
}

.tool {
    color: $toolColor;
    padding: 6px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    transition: all 0.2s ease-in-out;
    cursor: pointer;

    &:hover {
        color: #007097;
        background-color: $toolHover;
        transition: all 0.2s ease-in-out;
    }

    &:active {
        background-color: $toolActive;
        transition: all 0.2s ease-in-out;
    }
}

.selects {
    overflow: scroll;
    max-height: 400px;
}